<template>
    <div class="m-furniture">
        <div class="m-furniture-search">
            <el-input
                placeholder=""
                v-model="name"
            >
                <template slot="prepend"></template>
                <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
        </div>

        <!--  -->
        <el-button
            icon="el-icon-set-up"
            type="primary"
            plain
            class="m-furniture-filter-switch"
            @click="toggleFilter"
        >
            <span v-if="filter_open"></span>
            <span v-else></span>
        </el-button>

        <!--  -->
        <RightSidebar class="m-furniture-filter" :class="{ on: filter_open }">
            <div class="note"></div>
            <el-select v-model="source" placeholder="" size="medium">
                <el-option
                    v-for="item in sourceList"
                    :key="item"
                    :label="item"
                    :value="item"
                >
                </el-option>
            </el-select>
            <div class="note"></div>
            <el-select v-model="maxLevel" placeholder="" size="medium">
                <el-option
                    v-for="level in levels"
                    :key="level"
                    :label="level + ''"
                    :value="level"
                >
                </el-option>
            </el-select>
            <div class="note"></div>
            <el-checkbox v-model="interactable"></el-checkbox>
            <div class="note"></div>
            <el-input
                v-model="environment"
                placeholder=""
                size="medium"
            ></el-input>
            <div class="note"></div>
            <el-input
                v-model="beauty"
                placeholder=""
                size="medium"
            ></el-input>
            <div class="note"></div>
            <el-input
                v-model="robustness"
                placeholder=""
                size="medium"
            ></el-input>
            <div class="note"></div>
            <el-input
                v-model="practicality"
                placeholder=""
                size="medium"
            ></el-input>
            <div class="note"></div>
            <el-input
                v-model="fun"
                placeholder=""
                size="medium"
            ></el-input>
            <el-button
            class="m-furniture-filter-button"
            type="primary"
            @click="toggleFilter"
        ></el-button>
            
        </RightSidebar>

        <!--  -->
        <el-tabs
            class="m-furniture-type"
            v-model="type"
            type="card"
            @tab-click="handleTabChange"
        >
            <el-tab-pane label="" name="0"></el-tab-pane>
            <el-tab-pane label="" name="1"></el-tab-pane>
            <el-tab-pane label="" name="2"></el-tab-pane>
            <el-tab-pane label="" name="3"></el-tab-pane>
            <el-tab-pane label="" name="4"></el-tab-pane>
        </el-tabs>

        <!-- ul -->
        <div class="m-furniture-subtype">
            <ul class="subctg" v-if="subCtgData.length">
                <li
                    v-for="subctg of subCtgData"
                    :key="subctg.name"
                    @mouseenter="handleHoverSubCtg($event, subctg)"
                    @mouseleave="handleLeaveSubCtg($event, subctg)"
                    @click="handleSelectSubCtg($event, subctg)"
                >
                    <div v-bind:class="subCtgClass(subctg)"></div>
                    <!-- <img :src="`${imgurl}building/TY/TY_Base_M_pool01_AB200.png`" alt=""> -->
                    <span>{{ subctg.name }}</span>
                </li>
            </ul>
            <p class="u-all" v-else @click="handleSelectSubCtg($event, '')">
                <img src="../assets/img/furniture/home.png" />
                <span></span>
            </p>
        </div>

        <!--  :max-height="tableMaxHeight" -->
        <el-table
            class="m-furniture-list"
            :data="listData"
            style="width: 100%"
            @sort-change="handleSort"
            v-loading="loading"
        >
            <el-table-column fixed prop="icon" label="">
                <template slot-scope="prop">
                    <el-image
                        class="u-img"
                        :images="{ url: getUrl(prop.row.attributes.img) }"
                        style="height: 80px"
                        :src="getUrl(prop.row.attributes.img)"
                        fit="contain"
                        v-if="prop.row.attributes.img"
                        @click="onPreview(prop.row.attributes.img)"
                    >
                    </el-image>
                    <div class="u-img-null" v-else></div>
                </template>
            </el-table-column>
            <el-table-column prop="name" label="">
                <template slot-scope="prop">
                    <el-popover
                        placement="top-start"
                        width="200"
                        trigger="hover"
                        class="u-haspop"
                        :content="prop.row.attributes.desc"
                        v-if="
                            prop.row.attributes.desc &&
                                prop.row.attributes.desc !== ''
                        "
                        :close-delay="20"
                        :open-delay="20"
                    >
                        <span
                            slot="reference"
                            style="border-bottom: 1px solid gray"
                            class="furniture-name"
                            :class="`quality-${prop.row.attributes.quality}`"
                            >{{ prop.row.attributes.name }}</span
                        >
                    </el-popover>
                    <span
                        class="furniture-name"
                        :class="`quality-${prop.row.attributes.quality}`"
                        v-else
                        >{{ prop.row.attributes.name }}</span
                    >

                    <el-tooltip
                        effect="dark"
                        content=""
                        placement="top"
                        v-if="prop.row.attributes.interact"
                    >
                        <span class="furniture-indicator interactable"></span>
                    </el-tooltip>
                    <el-tooltip
                        effect="dark"
                        content=""
                        placement="top"
                        v-if="prop.row.attributes.scaleRange"
                    >
                        <span class="furniture-indicator scaleable"></span>
                    </el-tooltip>
                </template>
            </el-table-column>
            <el-table-column
                prop="attributes.level"
                label=""
                sortable="custom"
            ></el-table-column>
            <el-table-column
                prop="attributes.limit"
                label=""
                sortable="custom"
            ></el-table-column>
            <el-table-column
                prop="attributes.beauty"
                label=""
                sortable="custom"
            ></el-table-column>
            <el-table-column
                prop="attributes.practicality"
                label=""
                sortable="custom"
            ></el-table-column>
            <el-table-column
                prop="attributes.robustness"
                label=""
                sortable="custom"
            ></el-table-column>
            <el-table-column
                prop="attributes.environment"
                label=""
                sortable="custom"
            ></el-table-column>
            <el-table-column
                prop="attributes.fun"
                label=""
                sortable="custom"
            ></el-table-column>
            <el-table-column
                prop="attributes.price"
                label=""
                sortable="custom"
            ></el-table-column>
            <el-table-column
                prop="attributes.source"
                label=""
            ></el-table-column>
        </el-table>
        <el-button
            class="m-archive-more"
            :class="{ show: hasNextPage }"
            type="primary"
            :loading="loading"
            @click="appendPage"
            ></el-button
        >
        <el-pagination
            class="m-archive-pages"
            :page-size="15"
            background
            :hide-on-single-page="true"
            @current-change="changePage"
            :current-page.sync="page"
            layout="total, prev, pager, next, jumper"
            :total="total"
        >
        </el-pagination>

        <!--  -->
        <el-image-viewer
            v-if="showViewer"
            :on-close="closeViewer"
            :url-list="srcList"
        />
    </div>
</template>

<script>
import { __ossMirror } from "@jx3box/jx3box-common/js/jx3box.json";
import typedata from "@/assets/data/furniture_types.json";
import { getFurnitures } from "../service/furniture";
import ElImageViewer from "element-ui/packages/image/src/image-viewer";

export default {
    name: "Furniture",
    props: [],
    data: function() {
        return {
            type: "0", // 
            subCtg: undefined, // 
            hover: 0,
            typeData: [],
            listData: [],
            loading: false,
            // tableMaxHeight: window.innerHeight - 371,

            // 
            imgurl: __ossMirror + "pic/furniture/",
            showViewer: false,
            srcList: [],

            // 
            source: "",
            sourceList: [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
            ],
            maxLevel: 15,
            levels: Array.from({ length: 15 }).map((_, i) => i + 1),
            interactable: false,
            environment: "",
            beauty: "",
            robustness: "",
            practicality: "",
            fun: "",
            name: "", //
            filter_open: false,

            // 
            orderBy: undefined,
            order: undefined,
            page: 1,
            size: 15,
            total: 0,
        };
    },
    watch: {
        filterableProperties() {
            this.update();
        },
        name : function (){
            if(this.name){
                this.type = '0'
                this.subCtg = undefined;
                this.update();
            }
        }
    },
    computed: {
        filterableProperties() {
            return `${this.maxLevel},${this.source},${this.interactable},${this.environment},${this.beauty},${this.robustness},${this.practicality},${this.fun}`;
        },
        subCtgData() {
            return typedata[this.type] || [];
        },
        subCtgClass() {
            return (subctg) => {
                const category = +this.type * 10000 + subctg.id * 100;
                const tmpClass = {
                    "bg-1":
                        subctg.attr.icon === "homelandbuildingfiltericon.png",
                    "bg-2":
                        subctg.attr.icon === "homelandbuildingfiltericon2.png",
                };
                const type =
                    category === this.subCtg
                        ? "checked"
                        : category === this.hover
                        ? "hover"
                        : "normal";
                const x = subctg.attr[type] % 19;
                const y = Math.floor(subctg.attr[type] / 19);
                tmpClass[`sprite-${x}-${y}`] = true;
                return tmpClass;
            };
        },
        hasNextPage() {
            return this.page * this.size < this.total;
        },
    },
    methods: {
        handleHoverSubCtg(e, subctg) {
            this.hover = +this.type * 10000 + subctg.id * 100;
        },
        handleLeaveSubCtg(e, subctg) {
            // 
            this.hover = 0;
        },
        handleTabChange(tab) {
            if (tab.name === "0") {
                this.subCtg = undefined;
                this.update();
            }
        },
        handleSelectSubCtg(e, subctg) {
            // 
            this.subCtg = +this.type * 10000 + subctg.id * 100;
            this.update();
        },
        handleSort({ prop, order }) {
            // 
            if (order) {
                this.orderBy = prop.replace("attributes.", "");
                this.order = order === "ascending" ? 1 : 0;
            } else {
                this.orderBy = undefined;
                this.order = undefined;
            }
            this.update();
        },
        loadData(append = false) {
            this.loading = true;
            return getFurnitures({
                category: this.subCtg,
                source: this.source === "" ? undefined : this.source,
                limit: this.maxLevel,
                order: this.order,
                orderBy: this.orderBy,
                size: this.size,
                page: this.page,
                interactable: this.interactable === true ? "1" : undefined,
                environment: this.environment,
                beauty: this.beauty,
                robustness: this.robustness,
                practicality: this.practicality,
                fun: this.fun,
                name: this.name,
            })
                .then((res) => {
                    if (append) {
                        this.listData.push(...res.data.data);
                    } else {
                        this.listData = res.data.data;
                    }
                    this.total = res.data.meta.total;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        update() {
            this.page = 1;
            this.loadData();
        },
        getUrl(rawUrl) {
            return this.imgurl + rawUrl.replace("home/", "");
        },
        appendPage() {
            this.page += 1;
            this.loadData(true);
        },
        changePage() {
            this.loadData().then(() => {
                window.scrollTo(0, 0);
            });
        },
        onPreview(url) {
            this.srcList = [url.replace("home", __ossMirror + "pic/furniture")];
            this.showViewer = true;
        },
        closeViewer() {
            this.showViewer = false;
        },
        toggleFilter() {
            this.filter_open = !this.filter_open;
        },
    },
    mounted: function() {
        this.loadData();
    },
    components: {
        ElImageViewer,
    },
    beforeCreate:function (){
        location.href = 'https://www.jx3box.com/app/furniture'
    }
};
</script>

<style lang="less">
@import "../assets/css/furniture.less";
</style>
